.foreground-circle { fill: none; stroke: #3498db; }

.foreground-circle-without-stroke-color { fill: none; }

.background-circle { fill: none; stroke: #fff; }

.circle-text { font-family: Arial,sans-serif; color: #fff; fill: #fff; font-size: 20px; }

.circle-container { overflow: visible; }

.point-circle { fill: #f0f8ff; }

.circle-icon.fa { font-size: 30px; fill: #fff; }

.selectric-wrapper { position: relative; cursor: pointer; }

.selectric-responsive { width: 100%; }

.selectric { border-radius: 0px; background: #f9f9f9; position: relative; overflow: hidden; transition: all 0.3s; border-radius: 20px; }

.selectric .label { display: block; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; margin: 0 38px 0 10px; font-size: 12px; line-height: 38px; color: #444; height: 38px; user-select: none; }

.selectric .button { display: block; position: absolute; right: 0; top: 0; width: 38px; height: 38px; color: transparent; }

.selectric .button:after { content: '\f107'; font-family: 'FontAwesome'; font-weight: 900; color: #7c7c7c; display: flex; align-items: center; justify-content: center; position: absolute; top: 0; right: 0; bottom: 0; left: 0; margin: auto; transition: all 0.3s; }

.selectric-open .button:after { transform: rotate(180deg); }

.selectric-focus .selectric { border-color: #aaaaaa; }

.selectric-open { z-index: 9999; }

.selectric-open .selectric { border-color: #53BF51 !important; }

.selectric-open .selectric-items { display: block; }

.selectric-disabled { filter: alpha(opacity=50); opacity: 0.5; cursor: default; user-select: none; }

.selectric-hide-select { position: relative; overflow: hidden; width: 0; height: 0; }

.selectric-hide-select select { position: absolute; left: -100%; }

.selectric-hide-select.selectric-is-native { position: absolute; width: 100%; height: 100%; z-index: 10; }

.selectric-hide-select.selectric-is-native select { position: absolute; top: 0; left: 0; right: 0; height: 100%; width: 100%; border: none; z-index: 1; box-sizing: border-box; opacity: 0; }

.selectric-input { position: absolute !important; top: 0 !important; left: 0 !important; overflow: hidden !important; clip: rect(0, 0, 0, 0) !important; margin: 0 !important; padding: 0 !important; width: 1px !important; height: 1px !important; outline: none !important; border: none !important; *font: 0/0 a !important; background: none !important; }

.selectric-temp-show { position: absolute !important; visibility: hidden !important; display: block !important; }

/* Items box */
.selectric-items { display: none; position: absolute; top: 100%; left: 0; background: #fff; z-index: -1; border-radius: 20px; box-shadow: 0 4px 20px rgba(198, 207, 217, 0.5); overflow: hidden; }

.selectric-items .selectric-scroll { height: 100%; overflow: auto; }

.selectric-above .selectric-items { top: auto; bottom: 100%; }

.selectric-items ul, .selectric-items li { list-style: none; padding: 0; margin: 0; font-size: 12px; line-height: 20px; min-height: 20px; }

.selectric-items li { display: block; padding: 10px; color: #666; cursor: pointer; position: relative; }

.selectric-items li.selected::before { content: '\f00c'; font-family: 'FontAwesome'; position: absolute; right: 5px; color: #53BF51; }

.selectric-items li:hover { background: #D5D5D5; color: #444; }

.selectric-items .disabled { filter: alpha(opacity=50); opacity: 0.5; cursor: default !important; background: none !important; color: #666 !important; user-select: none; }

.selectric-items .selectric-group .selectric-group-label { font-weight: bold; padding-left: 10px; cursor: default; user-select: none; background: none; color: #444; }

.selectric-items .selectric-group.disabled li { filter: alpha(opacity=100); opacity: 1; }

.selectric-items .selectric-group li { padding-left: 25px; }

body { font-family: 'Montserrat', sans-serif; box-sizing: border-box; position: relative; font-weight: 500; font-size: 16px; }

.container { box-sizing: border-box; }

@media only screen and (min-width: 1200px) { .container { max-width: 1110px !important; } }

a { text-decoration: none; transition: all 0.3s; }

input[type="submit"] { appearance: none; -webkit-appearance: none; }

textarea { resize: none; }

@media only screen and (max-width: 767px) { .hidden-xs { display: none !important; } }

@media only screen and (min-width: 768px) and (max-width: 1024px) { .hidden-sm { display: none !important; } }

@media only screen and (min-width: 1024px) { .hidden-md { display: none !important; } }

i.icon-play { width: 50px; height: 50px; display: flex; position: relative; background: #fff; border-radius: 50%; }

i.icon-play::before { content: ''; position: absolute; top: 50%; left: 60%; transform: translate(-50%, -50%); border: 7px solid transparent; border-left: 12px solid #53BF51; }

.play-video { display: flex; justify-content: center; align-items: center; height: 180px; width: 110px; }

.play-video i { background: #53BF51; display: flex; flex-direction: column; justify-content: center; align-items: center; }

.play-video i:before { border-left: 12px solid #fff; z-index: 2; }

.play-video i span { position: absolute; border-radius: 50%; border: 1px solid #53BF51; animation: pulse 3s linear infinite; width: calc(100% + 40px); height: calc(100% + 40px); transition: all 0.5s; z-index: 1; }

.play-video i span:nth-child(2) { animation-delay: .7s; }

.play-video i span:nth-child(3) { animation-delay: 1.5s; }

.play-video i span:nth-child(4) { animation-delay: 2.3s; }

.play-video:hover span { animation-play-state: paused; }

.play-video:hover b { text-decoration: underline; }

.play-video b { position: absolute; bottom: 0px; display: block; width: 90px; text-align: center; font-weight: bold; font-size: 16px; line-height: 24px; color: #53BF51; }

@keyframes pulse { 0% { transform: scale(0.5);
    opacity: 1; }
  100% { transform: scale(1.2);
    opacity: 0; } }

.button-green { background: linear-gradient(112.31deg, #53BF51 0%, #8BDC8A 100%); box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3); border-radius: 40px; display: inline-block; padding: 15px 60px; font-size: 16px; font-weight: bold; color: #FFFFFF; transition: all 0.3s; }

.button-green:hover { box-shadow: 0px 5px 10px 0px rgba(198, 207, 217, 0.7); }

.button-transparent { font-weight: bold; font-size: 16px; color: #53BF51; padding: 13px 40px; border: 2px solid #53BF51; border-radius: 30px; display: inline-flex; }

.button-transparent:hover { background: #53BF51; color: #fff; }

